

.header{
    height: 60px;
    background-color: var(--shark);
}

.header__container{
    width: 95%;
    max-width: 900px;
    margin: 0px auto;
}

.header__wrapper{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 0px;
}


.header__title h2{
    font-family: 'Roboto', sans-serif;
    color: var(--white);
    font-size: 32px;
    line-height: 32px;
}

.header__search-wrapper{
    width: 75%;
    max-width: 360px;
    position: relative;
}

.header__search input{
    padding: 10px 42px;
    background-color: var(--middle-shark);
    color: var(--white);
}

.header__user-list{
    padding: 10px 0px;
    top: 45px;
    left: -15%;
    position: absolute;
    background-color: var(--middle-shark);
    width: 130%;
    max-width: 500px;
    max-height: 600px;
    overflow-y: auto;
}

.searched-user{
    cursor: pointer;
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 10px 15px;

    border-bottom: 1px solid var(--white01);
    transition: .1s;
}

.searched-user:last-child{
    border: none;
}

.searched-user:hover{
    background-color: var(--charade);
}

.searched-user__img{
    width: 40px;
    height: 40px;
}

.searched-user__img img{
    width: 100%;
    height: 100%;
    border-radius: 50%;
}

.searched-user__description{
    font-family: 'Roboto', sans-serif;
}

.searched-user__name{
    color: var(--white);
    font-size: 18px;
    line-height: 24px;
}

.searched-user__email{
    color: var(--white07);
    font-size: 16px;
    line-height: 24px;
}



.header__search svg{
    position: absolute;
    left: 12px;
    top: 11px;
    fill: white;

    transition: .1s;
}

.header__search input:focus{
    border-color: var(--conifer);
}

.header__search input:focus ~ svg{
    fill: var(--conifer);
}


.header__user{
    min-width: 40px;
    
    max-width: 40px;
    height: 40px;
}

.header__user img{
    width: 100%;
    height: 100%;
    border-radius: 50%;
}

