



.chat-body{
    width: 100%;
    height: 100%;
}

.chat-body__header{
    height: 60px;
    background-color: var(--shark);
    border-top: 1px solid var(--white01);
    border-bottom: 1px solid var(--white01);
    padding-left: 15px;

    display: flex;
    align-items: center;
}

.chat-user{
    display: flex;
    align-items: center;
    gap: 10px;
}

.chat-user__img{
    width: 40px;
    height: 40px;
}

.chat-user__img img{
    border-radius: 50%;
    width: 100%;
    height: 100%;
}

.chat-user__description{
    font-family: 'Roboto', sans-serif;
    font-size: 18px;
    color: var(--white);
}

.chat-body__body{
    overflow-y: auto;
    height: calc(100% - 60px - 80px);
}


.chat-body__messages{
    width: 90%;
    margin: 0px auto;
    height: calc(100% - 60px - 60px);
    padding: 10px 0px;

    color: var(--white);
    font-family: 'Open Sans', sans-serif;
    font-size: 16px;
}

.message{
    margin-bottom: 10px;
}

.message p{
    width: 48%;
    padding: 10px;
}

.chat-body__own-message{
    display: flex;
    justify-content: flex-end;
}

.chat-body__alien-message{
    display: flex;
    justify-content: flex-start;
}

.chat-body__own-message p{
    background-color: var(--conifer09);
    border-radius: 10px 10px 1px 10px;
}

.chat-body__alien-message p{
    background-color: var(--shark);
    border-radius: 10px 10px 10px 1px;
}


.chat-body__input-wrapper{
    padding: 15px 0px
}


.chat-body__form{
    position: relative;
    margin: 0px auto;
    width: 80%;
}

.chat-body__form input{
    padding: 15px 20px;
    background-color: var(--shark);
    color: var(--white);
}


.chat-body__form .btn{
    position: absolute;
    height: 50px;
    width: 50px;
    border-radius: 20px;
    right: 0px;
    top: 0px;

    background-color: transparent;

}

.chat-body__form .btn:hover svg{
    fill: var(--conifer);
}